import React, { Component } from "react";
import MovieList from "../movie_list/movie_list";
import MovieSearch from "../search/search";
import axios from "axios";
import Pagination from "../pagination/pagination";
import Filters from "../filters/filters";

export default class MovieListContainer extends Component {
  state = {
    movieList: [],
    searchList: [],
    value: "",
    searchBlock: false,
    page: 1,
    year: "",
    adult: false,
    results: ""
  };

  componentDidMount() {
    axios
      .get(
        `https://api.themoviedb.org/3/movie/now_playing?api_key=986b7e378730cbff3e87b1545000450c&language=en-US&page=${this.state.page}
                `
      )
      .then(res => {
        const movieList = res.data.results;
        this.setState({
          movieList,
          searchBlock: false
        });
      });
  }

  searchMovie = value => {
    const url =
      "https://api.themoviedb.org/3/search/movie?api_key=986b7e378730cbff3e87b1545000450c&language=en-US&query=";
    this.setState(
      {
        value
      },
      () => {
        if (this.state.value.length < 1) {
          this.setState({
            searchBlock: false,
            page: 1
          });
        } else {
          axios
            .get(
              `${url}${this.state.value}&page=${this.state.page}&include_adult=${this.state.adult}&year=${this.state.year}`
            )
            .then(res => {
              const searchList = res.data.results;
              const resultsCount = res.data.total_results;

              this.setState({
                searchList,
                searchBlock: true,
                results: resultsCount
              });

              if (searchList.length < 1) {
                this.setState({
                  results: "0"
                });
              }
            });
        }
      }
    );
  };

  nextPage = () => {
    this.setState({
      page: this.state.page + 1
    });

    this.searchMovie(this.state.value);
  };

  prevPage = () => {
    this.setState(
      {
        page: this.state.page - 1
      },
      () => {
        if (this.state.page < 1) {
          this.setState({
            page: 1
          });
        } else {
          this.searchMovie(this.state.value);
        }
      }
    );
  };

  yearHandler = year => {
    this.setState(
      {
        year: year
      },
      () => {
        this.searchMovie(this.state.value);
      }
    );
  };

  adultHandler = adult => {
    this.setState(
      {
        adult: adult
      },
      () => {
        this.searchMovie(this.state.value);
      }
    );
  };
  render() {
    return (
      <React.Fragment>
        <MovieSearch searchMovie={this.searchMovie} />

        <Filters
          yearHandler={this.yearHandler}
          adultHandler={this.adultHandler}
          show={this.state.searchBlock}
        />

        <MovieList
          movieList={this.state.searchList}
          show={this.state.searchBlock}
          title={`Results: ${this.state.results}`}
        />

        <MovieList
          movieList={this.state.movieList}
          show={!this.state.searchBlock}
          title="Now Playing:"
        />

        <Pagination
          nextPage={this.nextPage}
          prevPage={this.prevPage}
          show={this.state.searchBlock}
        />
      </React.Fragment>
    );
  }
}
